body{
  background-color: cadetblue;
}
.App{
  background-color: aliceblue;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
  border-radius: 10px;
}
#to-do-form input{
  background-color: darkkhaki;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: white;
}
#to-do-form input::placeholder{
  color: black;
}
#to-do-form input:focus, #to-do-form button:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: chartreuse;
  cursor: pointer;
  font-weight: bolder;
  color: white;
}